
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {mapMutations} from 'vuex'
import {time, phone} from '../config'
export default {
  name: 'phone',
  head: {
    title: {
      inner: '修改手机号码'
    }
  },
  data () {
    return {
      isReg: false,
      loading: false, // 注册按钮显示加载
      getting: false, // 获取验证码显示加载
      show: false, // 倒计时
      start: false,
      time: time,
      smsType: 2,
      text: '发送验证码',
      pwd: '',
      form: {
        tel: '',
        userId: JSON.parse(this.$localStorage.get('userInfo')).userId,
        captcha: ''
      }
    }
  },
  components: {
    Group,
    Cell,
    XInput,
    XButton,
    Countdown
  },
  methods: {
    handleTips () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: this.time + '秒后才能重新获取！',
        time: '1000'
      })
    },
    handleTime () {
      this.show = false
      this.start = false
      this.time = time
    },
    handleSendSms () {
      this.postSMS(this)
    },
    handleSubmit () {
      this.loading = true
      this.$http({
        method: 'jsonp',
        url: phone,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          phone: this.form.tel,
          captcha: this.form.captcha,
          userId: this.form.userId
        }
      })
      .then(res => {
        this.loading = false
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '20em',
            position: 'bottom',
            text: '手机号修改成功！',
            time: '1000'
          })
          setTimeout(() => {
            this.$localStorage.remove('userInfo')
            this.$localStorage.set('logined', false)
            this.$router.replace('/login')
          }, 1000)
        } else {
          this.$vux.toast.show({
            type: 'text',
            width: '20em',
            position: 'bottom',
            text: res.body.msg,
            time: '1000'
          })
        }
      })
    },
    ...mapMutations({
      postSMS: 'postSMS'
    })
  }
}



